.Card {
  width: 90vw;
  font-weight: 300;
  overflow: hidden;
  border-radius: 10px;
  color: var(--light);
  margin: 2rem auto;
}
.Recipe {
  z-index: 2;
}
.Recipe h4 {
  border-bottom: 1px solid var(--light);
  text-transform: uppercase;
}
.Image {
  background-size: cover;
  z-index: 1;
  -webkit-filter: brightness(60%);
          filter: brightness(60%);
}
.Recipe h3,
.Recipe h4 {
  letter-spacing: 0.2rem;
}
.Recipe h3 {
  font-family: "Orbitron", sans-serif;
  color: var(--light);
}
@media (min-width: 751px) {
  .Card {
    position: relative;
    display: block;
    line-height: 1.7;
    max-width: 800px;
  }

  .Recipe {
    position: relative;
    width: 50%;
    padding: 0.5rem 9rem 2rem 2rem;
    background: -webkit-gradient(
      linear,
      left top, right top,
      color-stop(40%, #212022),
      color-stop(80%, #100f0f),
      to(transparent)
    );
    background: -webkit-linear-gradient(
      left,
      #212022 40%,
      #100f0f 80%,
      transparent 100%
    );
    background: -o-linear-gradient(
      left,
      #212022 40%,
      #100f0f 80%,
      transparent 100%
    );
    background: linear-gradient(
      to right,
      #212022 40%,
      #100f0f 80%,
      transparent 100%
    );
  }

  .Recipe h3 {
    font-size: 30px;
  }

  .Recipe div {
    margin-left: 15px;
  }

  .Recipe ul {
    list-style: none;
    margin-left: -25px;
  }

  .Image {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
  }
}
@media (max-width: 750px) {
  .Card {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column-reverse;
        flex-flow: column-reverse;
    line-height: 1.6;
  }
  .Image {
    width: 100%;
    height: 250px;
    -webkit-box-shadow: inset 0px -50px 50px -5px #090909;
            box-shadow: inset 0px -50px 50px -5px #090909;
  }
  .Recipe {
    padding: 0.5rem 1rem 2rem 1rem;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(20%, #282829), color-stop(90%, #090909));
    background: -webkit-linear-gradient(bottom, #282829 20%, #090909 90%);
    background: -o-linear-gradient(bottom, #282829 20%, #090909 90%);
    background: linear-gradient(to top, #282829 20%, #090909 90%);
    font-size: 1.1rem;
  }
}



